import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";

import "./sass/app.scss";

import TopSection from "./components/top/index";
import BottomSection from "./components/bottom/index";

import axios from "axios";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      playerId: "101908548",
      // test with another id with 86956901
      numPlayerMatches: 4,
      isLoading: true,
    };
  }

  componentDidMount() {
    const { eventEmitter } = this.props;

    this.updatePlayer();
    this.getMatches();

    eventEmitter.on("updatePlayer", (data) => {
      this.setState({ playerId: data }, () => this.updatePlayer());
      console.log("PlayerID:", data);
    });
  }

  updatePlayer() {
    const { playerId } = this.state;
    const URL = `https://api.opendota.com/api/players/${playerId}`;
    const playerMatches = this.getMatches(playerId);
    axios
      .get(URL)
      .then((res) => {
        return res.data;
      })
      .then((data) => {
        this.setState({
          isLoading: false,
          profileName: data.profile.personaname,
          profilePicURL: data.profile.avatarmedium,
          rank_tier: data.rank_tier,
          mmr_estimate: data.mmr_estimate.estimate,
          playerMatches: playerMatches,
        });
      })
      .catch((err) => {
        if (err) console.error("Cannot fetch Player Data from API, ", err);
      });
  }

  getMatches(playerId) {
    const { numPlayerMatches } = this.state;
    const URL = `https://api.opendota.com/api/players/${playerId}/recentMatches`;
    axios
      .get(URL)
      .then((res) => {
        console.log("MATCHES: ", res.data);
        return res.data;
      })
      .then((data) => {
        for (var i = 0; i <= numPlayerMatches; i++) {
          if (data[i].player_slot >= 0 && data[i].player_slot < 5) {
            if (data[i].radiant_win == true) {
            }
            data[i].playerWin = true;
          } else if (data[i].player_slot >= 128 && data[i].player_slot < 133) {
            if (data[i].radiant_win == true) {
            }
            data[i].playerWin = false;
          }
        }

        this.setState({
          //set state with latest matches
          //hero id to hero
          playerMatches: data.slice(0, numPlayerMatches),
        });
      })
      .catch((err) => {
        if (err) console.error("Cannot fetch match Data from API, ", err);
      });
  }

  render() {
    const {
      isLoading,
      profileName,
      profilePicURL,
      mmr_estimate,
      rank_tier,
      playerMatches,
    } = this.state;
    return (
      <div className="app-container">
        <div className="main-container">
          {isLoading && <h3>Loading Player...</h3>}
          {!isLoading && (
            <div className="top-section">
              <TopSection
                profileName={profileName}
                mmr_estimate={mmr_estimate}
                rank_tier={rank_tier}
                profilePicURL={profilePicURL}
                eventEmitter={this.props.eventEmitter}
              />
            </div>
          )}
          <div className="bottom-section">
            <BottomSection playerMatches={playerMatches} />
          </div>
        </div>
      </div>
    );
  }
}

export default App;
