import React from "react";

import ProfileImg from "../../resources/images/profilePic.jpg";

export default class Dota extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    const {
      profileName,
      profilePicURL,
      rank_tier,
      mmr_estimate,
      playerMatches,
    } = this.props;
    return (
      <div className="dota-container">
        <div className="header">{profileName}</div>
        <div className="inner-container">
          <div className="image">
            <img src={profilePicURL} />
          </div>
          <div className="current-mmr">Current MMR: {mmr_estimate}</div>
        </div>
        <div className="footer">Rank Tier: {rank_tier}</div>
        <div className="playerMatches">{playerMatches}</div>
      </div>
    );
  }
}
