import React from "react";
import "./style.scss";

import Dota from "./dota";

import { Manager, Reference, Popper } from 'react-popper';

export default class TopSection extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isSelectPlayerOpen: false,
        }
    }


    onToggleSelectPlayer() {
        this.setState((prevState) => ({ isSelectPlayerOpen: !prevState.isSelectPlayerOpen }));
    }


    onPlayerIdChange(e) {
        this.setState({ playerId: e.target.value });
    }
    onSelectPlayer() {
        const { playerId } = this.state;
        const { eventEmitter } = this.props;

        eventEmitter.emit("updatePlayer", playerId);
        // close the pop up once the "select" button is clicked
        this.setState({ isSelectPlayerOpen: false })
    }


    render() {

        const { isSelectPlayerOpen } = this.state;
        const { eventEmitter } = this.props;
        return (
            <div className="top-container">
                <div className="title">Dota 2</div>
                <Dota {...this.props} />
                <Manager>
                    <Reference>
                        {({ ref }) => (
                            <button className="btn btn-select-player" ref={ref} onClick={this.onToggleSelectPlayer.bind(this)}>
                                Select Player</button>
                        )}
                    </Reference>
                    <Popper placement="top">
                        {({ ref, style, placement, arrowProps }) =>
                            isSelectPlayerOpen && (
                                <div
                                    className="popup-container"
                                    ref={ref}
                                    style={style}
                                    data-placement={placement}>
                                    <div className="form-container">
                                        <label htmlFor="player-id">Player ID</label>
                                        <input
                                            id="player-id"
                                            type="text"
                                            placeholder="SteamID3"
                                            onChange={this.onPlayerIdChange.bind(this)} />
                                        <button className="btn btn-select-player" onClick={this.onSelectPlayer.bind(this)}>Select</button>
                                    </div>
                                    <div ref={arrowProps.ref}
                                        style={arrowProps.style} />
                                </div>
                            )}
                    </Popper>
                </Manager>
            </div >
        );
    }
}