import React from "react";

import "../../resources/images/assets/stylesheets/dota2minimapheroes.css";

export default class PlayerMatch extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    const { match } = this.props;
    return (
      <div className="playerMatch-container">
        <div className="image">
          {/* <img
            //must use "require"
            src={require("C:/Users/Moonsoo Jo/Desktop/test/react-api/src/resources/images/snapfire.png")}
          ></img> */}
          <i class={"d2mh hero-" + match.hero_id}></i>
        </div>
        {match.playerWin ? (
          <div className="win">WIN</div>
        ) : (
          <div className="loss">LOSS</div>
        )}
      </div>
    );
  }
}
